import svg4everybody from 'svg4everybody';
import $ from 'jquery';
// import './lib/jquery.magnific-popup.js';
// import './lib/slick.js';
import './lib/owl.carousel.js';
// import './lib/jquery-ui.js';
// import './lib/jquery.nicescroll.js';

$(() => {
	svg4everybody();
});

$('.top-slider').owlCarousel({
		animateOut: 'fadeOut',
		animateIn: 'fadeIn',
		mouseDrag: false,
		touchDrag:false,
		loop:true,
		nav: true,
		dots: true,
		items: 1,
		navText: [,],
		autoplay: true,
		autoplayHoverPause: true,
		autoplayTimeout:8000,
		autoplaySpeed: 5000,
	});

$('.hit-slider').owlCarousel({
		mouseDrag: true,
		touchDrag:true,
		margin: 5,
		loop:true,
		nav: true,
		dots: false,
		items: 4,
		navText: [,],
		autoplay: true,
		autoplayHoverPause: true,
		autoplayTimeout:10000,
		autoplaySpeed: 10000,
	});


